<template>
  <div class='product-detail-wrapper'>
    <div class="product-detail-header">
      <div class="header-left">
        <span class="solution-top">产品详情</span>
        <img src="../../assets/images/back.png" class="back" title="返回上一层" @click="back">
      </div>
      <div class="header-right">
      </div>
    </div>
    <div class="product-detail-content">
      <div class="product-info-wrapper">
        <div class="product-info-header">
          <div class="product-info-title">
            产品基本信息
          </div>
          <div class="split-line"></div>
        </div>
        <div class="product-info-content">
          <div class="product-names">
            <img :src="productInfo.logo || require('../../assets/images/product_logo.png')" class="product-logo">
            <div class="product-base-info">
              <div class="product-name">{{ productInfo.chName }}</div>
              <div class="product-eName">{{ productInfo.enName }}</div>
              <div class="keywords">{{ productInfo.keyWord }}</div>
            </div>     
            </div>
          <button class="buy-btn" @click="buyProduct">购买激活码</button>
        </div>
      </div>
      <div class="product-info-wrapper">
        <div class="product-info-header">
          <div class="product-info-title">
            下载
          </div>
          <div class="split-line"></div>
        </div>
        <div class="product-info-content">
          <div class="download-area">
            <div class="download-zip" v-for="(item, index) in products" :key="index">
              <a :href="item.url" download @click="downloadPackage(item.id)">
              <img src="../../assets/images/download_zip.png" class="download-img">
              <div class="edition">{{ item.systemOs }}</div>
              </a>
            </div>
            
          </div>
          <div class="download-info">
            <div style="margin-bottom: 10px;">下载次数：{{ productInfo.totalDownloads}}次</div>
            <div>激活次数：{{ productInfo.activateTimes}}次</div>
          </div>
        </div>
      </div>
      <div class="product-info-wrapper">
        <div class="product-info-header">
          <div class="product-info-title">
            预览
          </div>
          <div class="split-line"></div>
        </div>
        <div class="product-info-content">
          <el-carousel type="card" :autoplay="false" indicator-position="none" height="175px;">
          <el-carousel-item v-for="(item, index) in preview_arr" :key="index" >
            <el-image 
              :src="item.imageUrl" 
              :preview-src-list="preview_arr_img" >
            </el-image>
            <!-- <img :src="item.imageUrl" class="pre-img"/> -->
          </el-carousel-item>
        </el-carousel>
        </div>
      </div>
      <div class="product-info-wrapper">
        <div class="product-info-header">
          <div class="product-info-title">
            产品简介
          </div>
          <div class="split-line"></div>
        </div>
        <div class="product-info-content">
          <div class="product-introduction">
            {{ productInfo.introduction}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
 
<script>
import { baseUrl } from "../../../config"
 import { getProductDetail, addDownloadTime } from "@/api/product"
export default {
  data() {
    return{
      baseUrl: "",
      productInfo: {
        chName: "",
        enName: "",
        id: "",
        keyWord: "",
        logo: "",
        totalDownloads: "",
        activateTimes: "",
        introduction: ""
      },
      products: [],
      download_times: 0,
      active_times: 0,
      preview_arr: []
    }
  },
  mounted() {
    this.baseUrl = baseUrl
    this.init();
  },
  computed: {
    preview_arr_img() {
      return this.preview_arr.map(item => item.imageUrl)
    }
  },
  methods: {
    buyProduct() {
      this.$router.push({
        name: 'productBuy',
        params: {
          id: this.$route.params.id
        }
      })
    },
    init() {
      getProductDetail({
        id: this.$route.params.id
      }).then(res => {
        if(res.data.code != 200) {
          this.$message({
            type: "warning",
            message: "该产品不存在或已下架"
          })
          this.$router.push("/productList")
        }
        this.productInfo = res.data.data.productSeries;
        this.productInfo.logo = this.baseUrl + "/file/"+this.productInfo.logo.replace(/.*\//g, "");
        this.products = res.data.data.products;
        this.products.forEach(item => {
          item.url = this.baseUrl + "/file/"+item.url.replace(/.*\//g, "");
        })
        this.preview_arr = res.data.data.images;
        this.preview_arr.forEach(item => {
          item.imageUrl = this.baseUrl + "/file/"+item.imageUrl.replace(/.*\//g, "");
        })
        
      })
    },
    downloadPackage(id) {
      addDownloadTime({
        id
      }).then(res => {
      })
      return false;
    },
    back() {
      window.history.back();
    }
  },
}
</script>
 
<style lang='scss' scoped>
  .product-detail-wrapper {
    .product-detail-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #dddddd;
      padding-bottom: 15px;
      .header-left {
        display: flex;
        align-items: center;
        background: url("../../assets/images/ico_navdot.png") no-repeat left 10px;
        .solution-top {
          // display: block;
          font-size: 20px;
          line-height: 36px;
          font-family: '微软雅黑';
          padding-left: 23px;
          color: #3b3b3b;
        }
        .back {
          margin-left: 10px;
          width: 35px;
          height: 35px;
          cursor: pointer;
        }
      }
      .header-right {
        display: flex;
        align-items: center;
        .el-input {
          width: 320px;
          margin-right: -4px;
          .el-input__inner {
            height: 36px;
          }
        }
        .el-button {
          width: 120px;
          z-index: 10;
          background-color: #3178bf;
          color: #fff;
          height: 36px;
          line-height: 36px;
          padding: 0;
          border: none;
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
      }
    }
    .product-detail-content {
      padding: 0 90px;
      .product-info-wrapper {
        margin-top: 30px;
        .product-info-header {
          display: flex;
          align-items: center;
          .product-info-title {
            font-size: 22px;
            color: #3b3b3b;
            font-weight: bold;
          }
          .split-line {
            flex: 1;
            height: 1px;
            background-color: #d6e4f2;;
            margin-left: 10px;
          }
        }
        .product-info-content {
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 0 100px;
          margin-top: 24px;
          .product-names {
            display: flex;
            align-items: center;
            .product-logo {
              width: 80px;
              height: 80px;
            }
            .product-base-info {
              margin-left: 30px;
              .product-name {
                font-size: 22px;
                color: #3c3e40;
              }
              .product-eName {
                font-size: 16px;
                color: #bdbdbd;
              }
              .keywords {
                font-size: 14px;
                color: #b4bac4;
              }
            }
          }
          .buy-btn {
            width: 160px;
            font-size: 18px;
            height: 40px;
            line-height: 40px;
            color: #fff;
            text-align: center;
            background-color: #3178bf;
            border-radius: 20px;
            border: none;
          }
          .download-area {
            display: flex;
            width: 450px;
            justify-content: space-between;
            .download-zip {
              text-align: center;
              cursor: pointer;
              .edition {
                font-size: 18px;
                color: #3178bf;
              }
            }
          }
          .download-info {
            font-size: 14px;
            color: #b4bac4;
          }
          .el-carousel {
            width: 100%;
            .pre-img {
              width: 100%;
              height: 100%;
            }
          }
          .el-carousel__mask {
            height: 100%;
          }
          .el-image {
            width: 100%;
            height: 100%;
          }
          .product-introduction {
            font-size: 18px;
            color: #636556;
            font-family: '微软雅黑';
          }
        }
      }
    }
  }
</style>