import fetch from "../utils/fetch"


export function getAllProduct() {
    return fetch({
        url: "/projectSeries/front/show",
        method: "get",
    })
}

export function createKey() {
    return fetch({
        url: "/projectSeries/newKey",
        method: "get",
    })
}

export function toggleProductStatus(data) {
    return fetch({
        url: "/projectSeries/onShelf",
        method: "put",
        data
    })
}
export function addProductSeries(data) {
    return fetch({
        url: "/projectSeries/addProductSeries",
        method: "put",
        data
    })
}

export function getProductDetail(data) {
    return fetch({
        url: "/projectSeries/front/getDetail/"+ data.id,
        method: "get",
    })
}

export function getProductShow() {
    return fetch({
        url: "/projectSeries/getProductShow",
        method: "get",
    })
}
export function addDownloadTime(data) {
    return fetch({
        url: "/product/front/down/"+ data.id,
        method: "get",
    })
}


